import React from 'react'

import { graphql } from 'gatsby'
import Helmet from 'react-helmet'
import styled from 'styled-components'

import Layout from '../components/Layout'
import MealPreview from '../components/MealPreview'

const ItemWrapper = styled.div`
  display: flex;
  padding: 20px;
  overflow-x: hidden;
  flex-wrap: wrap;
  background-color: #fff;
  max-width: 1180px;

  @media (max-width: 610px) {
    max-width: 328px;
  }

  @media (min-width: 611px) and (max-width: 895px) {
    max-width: 610px;
  }

  @media (min-width: 896px) and (max-width: 1180px) {
    max-width: 895px;
  }
`

interface IProps {
  pageContext: IPageContext
  data: ISite & IAllContentfulMeals
}

const IndexPage: React.FC<IProps> = ({ pageContext, data }) => (
  <Layout pageContext={pageContext}>
    <Helmet title={data.site.siteMetadata.title} />
    <ItemWrapper>
      {data.allContentfulMeal.edges.map(({ node }) => (
        <MealPreview key={node.slug} meal={node} locale={pageContext.locale} />
      ))}
    </ItemWrapper>
  </Layout>
)

export default IndexPage

export const pageQuery = graphql`
  query IndexByLanguage($locale: String!) {
    site {
      siteMetadata {
        title
      }
    }
    allContentfulMeal(
      filter: { node_locale: { eq: $locale }, title: { ne: null } }
      sort: { fields: publishDate, order: DESC }
    ) {
      edges {
        node {
          title
          slug
          shortDescription {
            childMarkdownRemark {
              html
            }
          }
          updatedAt
          heroImage {
            resolutions(width: 265) {
              ...GatsbyContentfulResolutions
            }
          }
        }
      }
    }
  }
`
